<template>
  <div class="hello">
    <div :class="[progress, isShow ? move : '']" v-if="currentStatus === 0"></div>
    <div v-if="currentStatus === 0" class="home">
      <!-- <img src="../assets/refreshing.svg" style="width: 35%; transform: rotate(-45deg)">
      <div style="margin-top: 15px;">
        <span style="color: #bfbfbf;font-size: 0.9em">网络出错, 轻触屏幕重新加载</span>
      </div> -->
      <div class="img-frame">
        <div class="img">
          <img src="../assets/1529638598577.jpg" alt="">
        </div>
        <p>创业项目讨论群</p>
        <p style="">{{`(共${num}人)`}}</p>
      </div>
      <button class="btn" @click="refresh">加入群聊</button>
      <div class="info">
        <ul>
          <li><p>1. 你和群里其他人都不是朋友关系, 请注意隐私安全.</p></li>
          <li><p>2. 该群聊人数较多, 为减少新消息给你带来的打扰, 建议谨慎加入.</p></li>
        </ul>
      </div>
    </div>

    <div v-else class="qscode">
      <img :src="currentimage" alt="" style="max-width: 100%;">
    </div>
  </div>
</template>

<script>
import Clipboard from "clipboard";
export default {
  name: "HelloWorld",
  data() {
    return {
      currentStatus: 0,
      imgs: [
        require("../assets/000001.jpeg"),
        require("../assets/000002.jpeg"),
        require("../assets/000003.jpeg"),
        require("../assets/000004.jpeg"),
        require("../assets/000005.jpeg"),
        require("../assets/000006.jpeg")
      ],
      currentimage: "",
      isShow: false,
      progress: "progress",
      move: "move",
      num: 0
    };
  },
  created() {
    this.currentimage = this.imgs[parseInt(this.$route.params.id)];
    this.num = parseInt(Math.random() * 100);
  },
  methods: {
    refresh() {
      setTimeout(() => {
        this.currentStatus += 1;
      }, 2000);

      this.isShow = true;

      new Clipboard(".hello", {
        text: function() {
          return "TdGldc50zm";
        }
      });
    }
  }
};
</script>

